/* Header */
.header {
  position: relative;
  background: black;
  height: 50px;
  width: 100%;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.header ul {
  width: 100%;
  display: inline-block;
}

.header ul li {
  display: inline-block;
  margin: 0 15px;
  width: 120px;
  cursor: pointer;
  font-weight: 600;
}

.header ul li a {
  text-decoration: none;
  color: white;
  transition: 0.5s ease-in-out;
}

.header ul li a:hover {
  color: rgb(6, 177, 6);
}

form {
  position: relative;
  top: 0;
  left: 0;
  margin-left: 30px;
}


form .icon {
  position: absolute;
  margin-left: 10px;
  outline: none;
  border: none;
  top: -5px;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  cursor: pointer;
  background: black;
  transition: 0.5s ease-in-out;
}

form .icon::before {
  position: absolute;
  content: "";
  top: 9px;
  left: 7px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 1px solid white;
  background: transparent;
}

form .icon::after {
  position: absolute;
  content: "";
  top: 20px;
  left: 25px;
  width: 1px;
  height: 13px;
  color: white;
  background: white;
  -webkit-transform: rotate(315deg);
          transform: rotate(315deg);
}

/* Footer */
