.overlay {
  display: none;
}


.overlay.active {
  display: block;
  background: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
  -webkit-animation: blowUpModal .25s cubic-bezier(0.165, 0.840, 0.440, 1.000) forwards;
          animation: blowUpModal .25s cubic-bezier(0.165, 0.840, 0.440, 1.000) forwards;
}

@-webkit-keyframes blowUpModal {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes blowUpModal {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
