.modal {
    display: none;
}

.modal.active {
    display: table;
    width: 50%;
    border-radius: 4px;
    background: rgb(240, 240, 240);
    position: fixed;
    top: 6%;
    left: 0;
    right: 0;
    z-index: 1;
    padding: 24px;
    margin: 0 auto;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-animation: blowUpModal .5s cubic-bezier(0.165, 0.840, 0.440, 1.000) forwards;
            animation: blowUpModal .5s cubic-bezier(0.165, 0.840, 0.440, 1.000) forwards;

}

.modal.out {
    display: block;
    width: 50%;
    border-radius: 4px;
    background: rgb(240, 240, 240);
    position: absolute;
    top: 5%;
    left: 0;
    right: 0;
    z-index: 1;
    padding: 24px;
    margin: 0 auto;
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-animation: blowUpModalTwo .5s cubic-bezier(0.165, 0.840, 0.440, 1.000) forwards;
            animation: blowUpModalTwo .5s cubic-bezier(0.165, 0.840, 0.440, 1.000) forwards;
}

